@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
*{
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Zilla Slab', serif;
  font-size: 30px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(130, 204, 221,1.0);
}

#box{
  background-color: rgba(12, 36, 97,1.0);
  max-width: 400px;
  min-width: 400px;
  max-height: 450px;
  min-height: 450px;
  color: whitesmoke;
  border-radius: 30px;
  padding: 15px;
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
}

#main-display{
  background-color: rgba(45, 52, 54,1.0);
  height: 60px;
  max-height: 60px;
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  padding-right: 5px;
}

#main-display p{
  font-size: 18px;
  text-align: right;
  overflow-wrap: break-word;
}

#main-display div{
  font-size: 30px;
  text-align: right;
  overflow-wrap: break-word;
}

#small-display{
  color: rgba(45, 52, 54,1.0);
}

#buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 10px;
}


.button {
  display: -webkit-flex;
  display: flex;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: 2px solid black;
  cursor: pointer;
  color: rgba(223, 230, 233,1.0);
  background-color: rgba(0, 184, 148,1.0);
  border-radius: 20px;
  font-size: 50px;
  margin: 4px;
  height: 60px;
  width: 70px;
}

#clear {
  width: 150px;
  margin-right: 160px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

